<template>
  <section class="debt-settlement">
    <!-- title -->
    <div class="text-center mb-2">
      <div class="d-flex align-items-center justify-content-center mb-2">
        <b-avatar class="mr-1" size="40" variant="light-danger">
          <img :src="require('@/assets/icons/building_icon.svg')" alt="" draggable="false" width="30" />
        </b-avatar>
        <h4 class="m-0">تسویه بدهکاری</h4>
      </div>
      <span> لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ  --  16</span>
    </div>

    <!-- main -->
    <!-- loader -->
    <div v-if="loading" class="d-flex flex-column justify-content-center align-items-center" style="margin-top: 150px">
      <img :src="require('@/assets/images/logo/logo.svg')" alt="Logo" width="70" />
      <b-spinner style="margin-top: 10px; width: 3rem; height: 3rem" variant="primary" />
    </div>

    <main>
      <b-card class="mb-5" v-if="!loading">
        <b-form v-if="getUserPrepaidDetails">
          <div class="card-items">
            <b-form-group label-for="name" label="نام شخص یا واحد مورد نظر">
              <b-form-input id="name" :value="getUserPrepaidDetails.full_name" type="text" readonly />
            </b-form-group>

            <b-form-group label-for="credit-amount" label="مبلغ بستانکاری" class="position-relative credit-amount">
              <b-form-input dir="ltr" id="credit-amount" :value="getUserPrepaidDetails.credit" readonly type="text" class="px-4" />
              <small class="currency-text"></small>
            </b-form-group>
          </div>

          <b-form-group class="reasons">
            <span class="label">بابت</span>
            <div v-for="(item, index) in reasons" :key="index" class="reasons-card">
              <div class="d-flex align-items-center justify-content-between">
                <div class="d-flex align-items-center justify-content-start">
                  <!-- check input -->
                  <div style="margin: 0 0px 0 27px">
                    <b-form-checkbox v-model="item.selected" :class="`custom-control-${item.color}`" />
                  </div>

                  <!-- icon -->
                  <div class="reasons-icon" :style="`background: ${item.color}`">
                    <feather-icon :icon="item.icon" size="17" />
                  </div>

                  <!-- name and date -->
                  <div class="mx-2">
                    <p style="color: #63587c; font-size: 14px">
                      {{ item.debt_name }}
                    </p>
                    <p style="color: #63587c; font-size: 12px">
                      {{ item.cycle_name }}
                    </p>
                  </div>
                </div>

                <!-- cost Fee -->
                <div>
                  <p style="color: #63587c; font-size: 14px">
                    {{ item.remain }}
                  </p>
                </div>
              </div>
            </div>
          </b-form-group>
        </b-form>

        <div class="mx-1 mt-auto">
          <b-button block variant="primary" @click="submit()">
            <img :src="require('@/assets/icons/add_icon.svg')" alt="add" draggable="false" width="24" class="add-icon" />
            ثبت تسویه بستانکاری
          </b-button>
        </div>
      </b-card>
    </main>
  </section>
</template>

<script>
import AddIcon from "@core/components/svg-icons/AddIcon.vue";
import {
  BButton,
  BCard,
  BFormGroup,
  BForm,
  BFormInput,
  BFormRow,
  BTable,
  BFormCheckbox,
  BAvatar,
  BSpinner

} from "bootstrap-vue";
import { getUserDebt, submitCreditCheckout } from '/src/api/costsServices'
import ToastificationContent from "@/@core/components/toastification/ToastificationContent.vue";

export default {
  name: "DebtSettlement",
  components: {
    BTable,
    BButton,
    BCard,
    BFormGroup,
    BForm,
    BFormInput,
    BFormRow,
    AddIcon,
    BAvatar,
    BFormCheckbox,
    BSpinner,
    ToastificationContent
  },
  data() {
    return {
      loading: false,
      reasons: [],
      selected: []
    }
  },
  async mounted() {
    this.getInfo()
  },
  computed: {
    getUserPrepaidDetails() {
      return this.$store.state.costs.getUserPrepaidDetails
    }
  },
  methods: {
    // get init info 
    async getInfo() {
      this.loading = true
      try {
        const { data } = await getUserDebt({
          params: {
            user_id: this.$route.params.id,
          }
        })
        this.reasons = data.list.map((item) => {
          return {
            "user_unit_debt_id": item.user_unit_debt_id,
            "icon": item.icon,
            "color": item.color,
            "debt_name": item.debt_name,
            "cycle_name": item.cycle_name,
            "remain": item.remain,
            "selected": false,
          }
        })
        this.loading = false
      } catch (error) {
        console.error(error)
      }
    },

    // store
    async submit() {
      let ts = this
      this.selected = []
      this.reasons.map((item) => {
        if (item.selected) ts.selected.push(item.user_unit_debt_id)
      })

      if (!this.selected.length) return this.$toast({
        component: ToastificationContent,
        props: {
          title: "حداقل یک مورد جهت تسویه انتخاب نمایید",
          icon: 'AlertCircleIcon',
          variant: 'danger',
        },
      })

      try {
        const { data } = await submitCreditCheckout({
          user_id: this.$route.params.id,
          user_unit_debt_id: this.selected
        })

        this.$toast({
          component: ToastificationContent,
          props: {
            title: data.message,
            icon: 'CheckSquareIcon',
            variant: 'success',
          },
        })
        this.getInfo()
      } catch (error) {
        console.error(error)
      }
    }
  }
}
</script>

